:root {
    --background-colour: #fafafa;
    --perimeterx-brand-colour: #ce0e2d;
    --block-padding: 2em;
}
* {
    margin: 0; padding: 0; border: 0;
    box-sizing: border-box;
}
html {
    height: 100%;
}
body {
    display: grid;
    grid-template-rows: auto 70vh;
    height: 100%;
    max-height: 100%;
    position: relative;
    background: var(--background-colour) url(./assorted-color-umbrella.jpg) fixed top center no-repeat;
    background-size: 100% auto;
}
body, input, select, button, textarea {
    font: 100 100%/1.8 -apple-system, "BlinkMacSystemFont", "Helvetica Neue", "Helvetica", "Lucida Grande", "Arial", sans-serif;
}
header, main {
    display: flex;
}
header {
    flex-direction: column;
    justify-content: center;
    background: rgba(0, 0, 0, .3);
    padding: 3em;
    text-align: center;
    color: white;
    text-shadow: -3px  3px 0 rgba(0, 0, 0, .8);
    box-shadow: inset 0 -10px 10px rgba(0, 0, 0, .5);
}
section, aside {
    background: var(--background-colour);
    box-shadow: 0px 0 30px -20px black;
    overflow: auto;
    min-width: 34vw;
    max-width: 50vw;
}
section {
    border: var(--block-padding) solid var(--background-colour);
}
aside {
    width: 32vw;
    min-width: 32vw;
    white-space: pre-line;
    border: var(--block-padding) solid black;
    font-family: monospace;
    background: black;
    color: rgb(0, 155, 0);
    transition: all .2s ease-out;
}
.logs aside {
    width: 0vw;
    min-width: 0vw;
    border-width: 0em;
}
@media (max-width: 1024px) {
    main {
        flex-direction: column;
    }
    section, aside {
        min-width: 100vw;
        max-width: 100vw;
    }
    .logs aside {
        display: none;
    }
}
aside p:nth-child(1) { color: rgb(0, 255, 0); }
aside p:nth-child(2) { color: rgb(0, 225, 0); }
aside p:nth-child(3) { color: rgb(0, 205, 0); }
aside p:nth-child(4) { color: rgb(0, 155, 0); }
a {
    color: inherit;
}
form {
    transition: filter .2s;
}
.loading form {
    filter: opacity(0.5);
}
input, select, button, textarea {
    border: 1px solid #ccc;
    background: white;
}
input, select {
    text-indent: .5em;
}
input:invalid {
    border-color: var(--perimeterx-brand-colour);
    background: #ffeaa7;
}
label {
    display: block;
    cursor: pointer;
    user-select: none;
    padding-bottom: .5em;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
}
textarea {
    width: 100%;
    max-width: 100%;
    height: 7em;
}
h3, p, label {
    margin: 0 0 .5em;
}
/**
 * Custom styles using custom className
 */
dialog.my-challenge-popup {
    font-size: .9em;
}
dialog.my-challenge-popup > div {
    border: 1px solid black;
    border-radius: 3px;
}
dialog.my-challenge-popup .title {
    font-weight: bold;
}
